.stars {
  display: flex;
  justify-content: space-between;
  &__star {
    background-image: url("../images/star-active.svg");
    background-repeat: no-repeat;
    width: 16px;
    height: 16px;
    margin: 1px 2px 0;
    cursor: pointer;
  }

  &__star--5 {
    background-image: url("../images/star.svg");
  }
}
